import { useMemo, useState } from 'react';

type OrderStatus = 'pending' | 'processing' | 'completed';

type CompletedType = 'done' | 'cancelled';

interface Order {
  id: string;
  customer: string;
  items: string; // summary
  total: number;
  createdAt: string; // ISO or human
  status: OrderStatus;
  completedType?: CompletedType;
}

const initialOrders: Order[] = [
  { id: 'ORD-1001', customer: 'Arun Kumar', items: 'Eggs x 12, Milk 1L', total: 220, createdAt: '2025-12-19 09:15', status: 'pending' },
  { id: 'ORD-1002', customer: 'Priya Sharma', items: 'Bread, Butter 200g', total: 180, createdAt: '2025-12-19 09:22', status: 'pending' },
  { id: 'ORD-1003', customer: 'Rahul Mehta', items: 'Rice 5kg', total: 650, createdAt: '2025-12-19 09:30', status: 'processing' },
  { id: 'ORD-1004', customer: 'Neha Patel', items: 'Oil 1L, Sugar 1kg', total: 320, createdAt: '2025-12-19 09:40', status: 'processing' },
  { id: 'ORD-1005', customer: 'Vikas Singh', items: 'Fruits pack', total: 240, createdAt: '2025-12-19 08:55', status: 'completed', completedType: 'done' },
  { id: 'ORD-1006', customer: 'Sneha Das', items: 'Curd 500g', total: 60, createdAt: '2025-12-19 08:40', status: 'completed', completedType: 'cancelled' },
];

export default function VendorOrders() {
  const [orders, setOrders] = useState<Order[]>(initialOrders);
  const [dragOver, setDragOver] = useState<OrderStatus | null>(null);
  const [draggingId, setDraggingId] = useState<string | null>(null);
  const [draggingFrom, setDraggingFrom] = useState<OrderStatus | null>(null);

  const columns = useMemo(() => ({
    pending: orders.filter((o) => o.status === 'pending'),
    processing: orders.filter((o) => o.status === 'processing'),
    completed: orders.filter((o) => o.status === 'completed'),
  }), [orders]);

  const canDropTo = (from: OrderStatus | null, to: OrderStatus): boolean => {
    if (!from) return false;
    if (from === 'pending' && to === 'processing') return true;
    if (from === 'processing' && to === 'completed') return true;
    return false; // completed is terminal
  };

  const onDragStart = (e: React.DragEvent, id: string, from: OrderStatus) => {
    setDraggingId(id);
    setDraggingFrom(from);
    e.dataTransfer.setData('text/plain', id);
    e.dataTransfer.effectAllowed = 'move';
  };

  const onDragOver = (e: React.DragEvent, to: OrderStatus) => {
    if (!draggingFrom) return;
    if (!canDropTo(draggingFrom, to)) return; // disallow default so column won't highlight
    e.preventDefault();
    e.dataTransfer.dropEffect = 'move';
    setDragOver(to);
  };

  const onDragLeave = (_e: React.DragEvent, to: OrderStatus) => {
    if (dragOver === to) setDragOver(null);
  };

  const onDrop = (e: React.DragEvent, to: OrderStatus, completedType?: CompletedType) => {
    e.preventDefault();
    const id = e.dataTransfer.getData('text/plain');
    setDragOver(null);
    if (!draggingFrom || !canDropTo(draggingFrom, to)) return;

    setOrders((prev) => prev.map((o) => {
      if (o.id !== id) return o;
      return { ...o, status: to, completedType: to === 'completed' ? (completedType ?? 'done') : o.completedType };
    }));

    setDraggingId(null);
    setDraggingFrom(null);
  };

  const advance = (id: string) => {
    setOrders((prev) => prev.map((o) => {
      if (o.id !== id) return o;
      if (o.status === 'pending') return { ...o, status: 'processing' };
      if (o.status === 'processing') return { ...o, status: 'completed', completedType: 'done' };
      return o;
    }));
  };

  const cancelOrder = (id: string) => {
    setOrders((prev) => prev.map((o) => (o.id === id ? { ...o, status: 'completed', completedType: 'cancelled' } : o)));
  };

  const rejectOrder = (id: string) => {
    setOrders((prev) => prev.map((o) => (o.id === id ? { ...o, status: 'completed', completedType: 'cancelled' } : o)));
  };

  const Column = ({ title, type }: { title: string; type: OrderStatus }) => (
    <section
      className={`orders-column ${dragOver === type ? 'drag-over' : ''}`}
      onDragOver={(e) => onDragOver(e, type)}
      onDragLeave={(e) => onDragLeave(e, type)}
      onDrop={(e) => onDrop(e, type)}
      aria-label={`${title} orders`}
    >
      <header className="orders-column-header">
        <h2 className="orders-column-title">{title}</h2>
        <span className="orders-count">{columns[type].length}</span>
      </header>
      <div className="orders-column-body">
        {columns[type].map((o) => (
          <article
            key={o.id}
            className={`order-card ${draggingId === o.id ? 'dragging' : ''}`}
            draggable={type !== 'completed'}
            onDragStart={(e) => onDragStart(e, o.id, type)}
            aria-grabbed={draggingId === o.id}
          >
            <div className="order-row">
              <div className="order-id">{o.id}</div>
              <div className="order-total">₹{o.total.toFixed(2)}</div>
            </div>
            <div className="order-customer">{o.customer}</div>
            <div className="order-items">{o.items}</div>
            <div className="order-meta">{o.createdAt}</div>
            {type === 'completed' ? (
              <div className="order-footer">
                <span className={`status-badge ${o.completedType === 'cancelled' ? 'cancelled' : 'done'}`}>
                  {o.completedType === 'cancelled' ? 'Cancelled' : 'Done'}
                </span>
              </div>
            ) : (
              <div className="order-footer">
                {type === 'pending' && (
                  <div className="btn-group">
                    <button className="btn-small primary" onClick={() => advance(o.id)}>Accept → Processing</button>
                    <button className="btn-small danger" onClick={() => rejectOrder(o.id)}>Reject</button>
                  </div>
                )}
                {type === 'processing' && (
                  <div className="btn-group">
                    <button className="btn-small primary" onClick={() => advance(o.id)}>Mark Done</button>
                    <button className="btn-small danger" onClick={() => cancelOrder(o.id)}>Cancel</button>
                  </div>
                )}
              </div>
            )}
          </article>
        ))}
      </div>
    </section>
  );

  return (
    <div className="orders-board" role="region" aria-label="Orders Kanban Board">
      <Column title="Pending" type="pending" />
      <Column title="Processing" type="processing" />
      <Column title="Completed" type="completed" />
    </div>
  );
}
